export default ".hud-container {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    color: white;\n    border: solid 1px white;\n    border-radius: 10px;\n    z-index: 1;\n}\n\n.hud-border {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    color: white;\n    border: solid 1px white;\n    border-radius: 10px;\n    background: gray;\n    opacity: 30%;\n    z-index: 0;\n}\n#hud {\n    position: absolute;\n    top: 5px;\n    left: 5px;\n    right: 5px;\n    bottom: 5px;\n    color: white;\n    border: solid 1px white;\n    border-radius: 10px;\n    opacity: 100%;\n    display: flex;\n    flex-direction: row;\n}\n.hud-left,\n.hud-right {\n    text-align: left;\n    vertical-align: middle;\n    padding: 5px;\n    font-size: 12px;\n    overflow: hidden;\n}\n.hud-right {\n    flex: 1;\n}\n\n.hud-left {\n    border-right: 1px solid white;\n    width: 130px;\n}\n.hud-left,\n.hud-right,\n.hud-left > div,\n.hud-right > div {\n    display: inline-block;\n}\n\n.warning {\n    color: yellow;\n}\n.danger {\n    color: red;\n    font-weight: bold;\n}\n";